import Brands from "../../components/Brands"
import Footer from "../../components/Footer"
import Navbar from "../../components/Navbar"
import reset, { cleanupAll } from "../../utils/reset"
import { trackPageVisit } from '../../utils/analtics'


const PRIVACY_SECTIONS = [
    {
        title: "General Information",
        content: `
            <p class="text-gray-300">
                Olymp AI Invest ("we", "us", "our") values your privacy and is committed to protecting your personal data. This privacy policy explains how we collect, use, and safeguard your information when you use our platform.
            </p>
            <p class="text-gray-300 mt-4">
                We collect information such as your name, email address, contact details, and other identifying information you provide when signing up for our services. We may also collect financial information necessary for processing transactions.
            </p>`
    },
    {
        title: "How We Use Your Information",
        content: `
            <p class="text-gray-300 mb-4">
                We use cookies and similar tracking technologies to enhance your experience and gather information about your interactions with our services.
            </p>
            <ul class="list-disc pl-6 space-y-3 text-gray-300">
                <li>To provide and maintain our trading services</li>
                <li>To process transactions and execute trades</li>
                <li>To communicate with you about updates and notifications</li>
                <li>To personalize and improve your platform experience</li>
                <li>To comply with legal and regulatory requirements</li>
            </ul>`
    },
    {
        title: "Data Security",
        content: `
            <p class="text-gray-300">
                We implement robust security measures to protect your information. However, no method of transmission over the internet is completely secure. While we strive to protect your data using industry-standard encryption and security protocols, we cannot guarantee absolute security.
            </p>`
    },
    {
        title: "Information Sharing",
        content: `
            <p class="text-gray-300 mb-4">
                We do not sell or rent your personal information to third parties. However, we may share your information with:
            </p>
            <ul class="list-disc pl-6 space-y-3 text-gray-300">
                <li>Service providers and partners who assist in providing our services</li>
                <li>Regulatory authorities when required by law</li>
                <li>Other platform users as necessary for transactions</li>
            </ul>`
    },
    {
        title: "Your Rights",
        content: `
            <ul class="list-disc pl-6 space-y-3 text-gray-300">
                <li>Access and update your personal information</li>
                <li>Request deletion of your data</li>
                <li>Opt-out of marketing communications</li>
                <li>Object to processing of your data</li>
            </ul>
            <p class="text-gray-300 mt-4">
                Contact our support team to exercise any of these rights.
            </p>`
    },
    {
        title: "Contact Us",
        content: `
            <p class="text-gray-300">
                If you have questions about this Privacy Policy, please contact us at:
            </p>
            <p class="text-gray-300 mt-4">
                Olymp AI Invest<br>
                Email: support@olympaiinvest.com<br>
                Address: 123 Trading Street, Financial District<br>
                New York, NY 10004
            </p>`
    }
]

const privacy = async () => {
    reset('Olymp AI Invests Privacy Policy')
    cleanupAll()

    await trackPageVisit()
    const { html: navbar, pageEvents: navEvents } = Navbar()

    function pageEvents() {
        navEvents()
    }

    return ({
        html: /* html */`
        ${navbar}
        <!-- Banner Section -->
        <section class="relative bg-cover bg-center py-20" 
                 style="background-image: url('https://softivus.com/wp/tradly/wp-content/uploads/2025/05/Breadcrumbs-Area-1-min.png')">
            <div class="absolute inset-0 bg-black/70"></div>
            <div class="container mx-auto px-4 relative z-10">
                <div class="text-center">
                    <h2 class="text-4xl md:text-5xl font-bold text-white mb-4">Privacy Policy</h2>
                    <nav aria-label="breadcrumb" class="flex justify-center">
                        <ol class="flex gap-2 text-gray-300">
                            <li><a href="/" data-nav class="hover:text-brand-primary">Home</a></li>
                            <li>&gt;</li>
                            <li class="text-brand-primary">Privacy Policy</li>
                        </ol>
                    </nav>
                </div>
            </div>
        </section>

        <!-- Privacy Content -->
        <section class="py-20 bg-brand-dark">
            <div class="container mx-auto px-4">
                <div class="max-w-4xl mx-auto">
                    <div class="bg-brand-blue/10 rounded-2xl p-8 md:p-12 border border-brand-primary/20">
                        <!-- Header -->
                        <div class="text-center mb-12">
                            <h2 class="text-2xl md:text-3xl font-bold text-white">
                                Olymp AI Invest Privacy Policy
                            </h2>
                            <p class="text-gray-400 mt-4">
                                Last updated: August 21, 2025
                            </p>
                        </div>

                        <!-- Privacy Sections -->
                        <div class="space-y-12">
                            ${PRIVACY_SECTIONS.map(section => `
                                <div class="privacy-section">
                                    <h3 class="text-xl font-bold text-brand-primary mb-6">
                                        ${section.title}
                                    </h3>
                                    <div class="space-y-4">
                                        ${section.content}
                                    </div>
                                </div>
                            `).join('')}
                        </div>

                        <!-- Footer Note -->
                        <div class="mt-12 pt-8 border-t border-brand-primary/20">
                            <p class="text-gray-400 text-sm">
                                By using Olymp AI Invest's services, you acknowledge that you have read and understood this Privacy Policy. We reserve the right to update this policy periodically.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        ${Brands()}
        ${Footer().html}
        `,
        pageEvents
    })
}

export default privacy